*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul, li {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

button, button:focus {
  outline: none;
}

hr {
  margin: 0;
}
